.loading {
    font-size: 36px;
    text-align:  center;
}

.loading:after {
    content: "\2026";
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    -webkit-animation: ellipsis-dot 1s infinite;
            animation: ellipsis-dot 1s infinite;
    -webkit-animation-fill-mode: fowards;
            animation-fill-mode: fowards;
    width: 1.25em;
}

@-webkit-keyframes ellipsis-dot {
    50% {
        width: 0em;
    }
    100% {
        width: 1.25em;
    }
}

@keyframes ellipsis-dot {
    50% {
        width: 0em;
    }
    100% {
        width: 1.25em;
    }
}

.album {
    width: 250px;
    height: 150px;
    background-color: bisque;
    display: inline-block;
    margin: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
}

.album:hover {
    background-color: aqua;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    cursor:pointer;
}

.album span {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.photo {
    width: 150px;
    height: 150px;
    display: inline-block;
    margin: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
}

.return {
    display: inline-block;
    text-decoration: none;
    background-color: #3b8235;
    color: white;
    padding: 10px;
}

.return:hover {
    background-color: #52af4a;
}

.addTag {
    font-size: 20px;
    font-weight: 700;
    padding: 5px 10px;
    color:#ffffff;
    background-color: #52af4a;
    position: absolute;
}

.addTag:hover {
    background-color: #af9e17;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    cursor:pointer;
}

.search-block {
    display: inline-block;
    margin: 10px;
    background-color: bisque;
}

.search-block span{
    display: inline-block;
    margin: 10px;
}

.search-field {
    display: inline-block;
    width: 50%;
    font-size: 16px;
}

